#scroll {
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  border: none;
  width: 100%;
  padding-bottom: 2rem;
}
#scroll .scroll__graphic {
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  position: absolute;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
  bottom: auto;
}
@media (max-width: 950px) {
  #scroll .scroll__graphic:before {
    margin-left: auto;
  }
  #scroll .scroll__graphic .chart {
    margin: 0px;
    width: 100%;
  }
}
#scroll .scroll__graphic:before {
  content: 'Skateboard Music by Genre';
  position: absolute;
  left: 0px;
  right: 0px;
  top: 20px;
  margin: 0 auto;
  margin-left: 40%;
  text-align: center;
  font-family: 'Atlas Grotesk Web';
  font-weight: 600;
  font-size: 16px;
  text-transform: uppercase;
}
#scroll .scroll__graphic .chart {
  position: absolute;
  width: 60%;
  background: #000;
  top: 50%;
  transform: translateY(-50%);
  margin: 0 auto;
  margin-left: 40%;
}
#scroll .scroll__graphic .chart svg {
  overflow: visible;
  display: block;
  position: relative;
}
#scroll .scroll__graphic .chart svg .maskRect {
  fill: #000;
  stroke: #ffa500;
  stroke-width: 6px;
  stroke-opacity: 0.3;
  shape-rendering: crispEdges;
  visibility: hidden;
  opacity: 1;
  transition: opacity 0.2s;
}
#scroll .scroll__graphic .chart svg .axis--x .tick line {
  stroke: #fff;
}
#scroll .scroll__graphic .chart svg .axis--x .tick text {
  font-size: 0.8rem;
  font-family: 'Atlas Grotesk Web';
  fill: #fff;
  font-weight: 600;
  fill: rgba(255,255,255,0.7);
}
#scroll .scroll__graphic .chart svg .activities {
  font-family: 'Atlas Grotesk Web';
}
#scroll .scroll__graphic .chart svg .activities .activity .percent-annotation {
  font-size: 11px;
  fill: #ccc;
}
#scroll .scroll__graphic .chart svg .activities .activity .line {
  stroke-width: 1.5px;
}
#scroll .scroll__graphic .chart svg .activities .activity .line-bottom {
  stroke: rgba(204,204,204,0.51);
  shape-rendering: crispEdges;
  stroke-dasharray: 2, 2;
}
#scroll .scroll__graphic .chart svg .activities .activity text {
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
}
#scroll .scroll__graphic .chart svg .axis--activity .tick text {
  font-size: 0.8rem;
  font-family: 'Atlas Grotesk Web';
  fill: rgba(0,0,0,0.8);
}
#scroll .scroll__text {
  position: relative;
  width: 25%;
  margin-left: 10%;
}
@media (max-width: 950px) {
  #scroll .scroll__text {
    width: 100%;
    margin: 0 auto;
  }
  #scroll .scroll__text .step {
    width: 90%;
    margin: 0 auto;
    margin-bottom: 100vh;
    background: rgba(0,0,0,0.8);
